@import '../colors.scss';

#swe {
    height: 90%;
    width: 80%;
    // background-color: lightcyan;
    // opacity: 0.5;
    margin-top: 3rem;
    color: $blueCeladon;
    text-shadow: $peachPuff 1px 0 7px;
    font-size: 1.6rem;


    #emphasize {
        // font-size: 2rem;
        color: $blueCG;
        font-weight: 600;
        text-shadow: $blueAero 1px 0 7px;
    }

    a {
        // font-size: 2rem;
        font-weight: 600;
        color: $blueGreenMax;
        text-shadow: white 1px 0 7px;
        text-decoration-color: $blueGreenMid;
        transition: all 0.3s ease-in;

        &:hover {
            // font-size: 1.1rem;
            // transform: scale(1.2);
            color: $blueCG;
            text-decoration: none;
            // text-decoration-color: $yellowLight;
            transition: all 0.3s ease-in;
        }
    }


    li {
        list-style-type: '\1F505'; //unicode for graduation cap
        margin-bottom: 30px;
        font-size: 0.8em;
        // text-shadow: 

        span {
            margin-left: 5px;
            font-size: 1.1em;
        }

    }

    #stack {
        columns: 2;
        -webkit-columns: 2;
        -moz-columns: 2;
        column-gap: 0;

        li {
            list-style: none;
            margin: 0;
        }
    }

}