@import '../colors.scss';

#portfolio {

    overflow: hidden;
    // background: linear-gradient(white, $peachPuff);
    // display: flex;
    // align-items: center;
    // justify-content: center;
    // font-family: 'Shadows Into Light', 'Raleway', sans-serif;
    background: $linen;

    #title {
        margin-top: 10px;
        display: flex;
        justify-content: center;
        text-transform: uppercase;
        letter-spacing: 15px;
        color: white;
        font-weight: 600;
        /* color | offset-x | offset-y | blur-radius */
        text-shadow: $blueSapphire 2px 2px 10px;
        // font-family: 'Exo';
    }
    // #titleContainer {
    //     width: 150px;
    //     height: 100vh;
    //     display: flex;
    //     align-items: center;
    //     justify-content: end;

    //     #title {
    //         writing-mode: vertical-rl;
    //         transform: rotate(180deg);
    //         text-transform: uppercase;
    //         letter-spacing: 5px;
    //         font-size: 2.9rem;
    //         color: $blueCG;
    //         /* offset-x | offset-y | color */
    //         text-shadow: 5px 5px $peachPuff;
    //         font-weight: 600;
    //     }
    // }

    #projectsContainer {
        width: calc(100vw - 150px);
        height: 100vh;
        // padding: 70px 30px 0px 30px;
        // margin-bottom: 70px;
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: center;
        overflow: hidden;
        position: relative;


        li {
            display: flex;
            flex-wrap: wrap;
            align-items: center;
            justify-content: center;
            list-style: none;

            #project {
                width: 100%;
                height: 100%;
                margin: 0px 30px 0px 30px;
                border-radius: 20px;
                display: flex;
                align-items: center;
                justify-content: center;
                flex-wrap: wrap;
                overflow: hidden;
                position: relative;
                background-color: none;
                opacity: 1;
                /* offset-x | offset-y | blur-radius | color */
                // box-shadow: 0 1rem 3rem rgba(224, 154, 88, 0.416);
                box-shadow: 0 1rem 3rem lightgrey;
                cursor: pointer;
                transition: all 1s ease;

                .projImage {
                    width: 100%;
                    height: 100%;
                    // border-radius: 20px;
                    object-fit: fill;
                    z-index: 1;
                }

                #projTitle {
                    position: absolute;
                    font-size: 30px;
                    color: white;
                    z-index: 2;
                    /* color | offset-x | offset-y | blur-radius */
                    text-shadow: $blueCG 1px 1px 8px;
                    top: 5px;
                    left: 10px;
                    // font-style: italic;
                }

                #demo {
                    position: absolute;
                    font-size: 20px;
                    color: white;
                    z-index: 0;
                    /* color | offset-x | offset-y | blur-radius */
                    text-shadow: $yellowLight 1px 1px 8px;
                    font-family: 'Exo', 'Raleway', sans-serif;
                    border-radius: 40px;
                    background-color: $tangerineVivid;
                    border-color: $bitterSweet;

                }

                #stack {
                    width: 100%;
                    position: absolute;
                    font-size: 30px;
                    z-index: 2;
                    background-color: $linen;
                    opacity: 0.7;
                    bottom: 0px;
                }

                &:hover {
                    // background-color: $linen;
                    background-color: rgb(252, 250, 248);
                    transform: scale(1.1);

                    .projImage {
                        opacity: 0.3;
                        z-index: 0;
                        // object-fit: fill;
                        transition: all 1s ease;
                    }

                    // #projTitle {
                    //     /* color | offset-x | offset-y | blur-radius */
                    //     text-shadow: $blueSapphire 1px 0 10px;
                    //     transition: all 1s ease;

                    // }

                    // #stack {
                    //     // position: absolute;
                    //     // font-size: 30px;
                    //     background-color: none;
                    // }
                }
            }
        }

        #nextpage {
            padding: 3px 235px 0 0;
        }

        .modal-dialog,
        .modal-content {
            width: 1400px;
            height: 650px;
            color: $blueSapphire;

            .modal-title {
                font-size: 3em;
                color: inherit;
            }

            .modal-body {
                font-size: 1.6em;
                color: inherit;
            }
        }
    }
}